<template lang="html">
<div class="">
  <h1>Administracija Testova</h1>
</div>
  <div class="overlay" >

    <TabNav :tabs="companyList" :selected="selected" :tabType="true" @selected="setSelected" id="companyNav">
      <Tab v-for="(company,i) in companyList" :key="company" :isSelected="selected === company ">

        <div v-if="loading" class="loader">

        </div>

        <div v-if="!loading" >
          <div class="validation">
            {{validation}}
          </div>

          <button  class="dd" type="button" name="button" @click="administerProcenaEdukacijeHere()" >Dodaj novi upitnik za procenu edukacije</button>
          <button  class="dd" type="button" name="button" @click="administerPerformanceEval(listOfListsOfUsers[i])" ><b>!!!</b>Dodaj Novi Performance Evaluation<b>!!!</b></button>
            <button  class="dd" type="button" name="button" @click="administerPerformanceEvalLicni(listOfListsOfUsers[i])" ><b>!!!</b>Dodaj Novi Performance Evaluation sa Istim licnim KPI<b>!!!</b></button>
          <p><b>!PAŽNJA NEPOVRATNA OPCIJA!</b> Kada se aktivira arhiviraju se odrađeni KPI i dodeljuju se novi. Kompanijski i kompetence će ostati, ali svi lični KPI moraju opet da se unose!</p>
          <div class="container" v-for="userX in listOfListsOfUsers[i]"  :key="userX._id" >
            <div class="userName">
              {{userX.name}}
            </div>
            <button  class="bb" type="button" name="button" @click="administerVQ(userX)" v-if="!userX.availableTest.vqTest">Vq test</button>
            <button  class="cc" type="button" name="button" @click="administerVQDeny(userX)" v-if="userX.availableTest.vqTest">Vq test</button>

            <button  class="bb" type="button" name="button" @click="administerTL(userX)" v-if="!userX.availableTest.testLicnosti">Test Licnosti</button>
            <button  class="cc" type="button" name="button" @click="administerTLDeny(userX)" v-if="userX.availableTest.testLicnosti">Test Licnosti</button>

            <button  class="bb" type="button" name="button" @click="administerStavovi(userX)" v-if="!userX.availableTest.stavoviZaposlenih">Stavovi Zaposlenih</button>
            <button  class="cc" type="button" name="button" @click="administerStavoviDeny(userX)" v-if="userX.availableTest.stavoviZaposlenih">Stavovi Zaposlenih</button>

            <button  class="bb" type="button" name="button" @click="administerFB(userX)" v-if="!userX.availableTest.feedbackTest">360 FeedBack</button>
            <button  class="cc" type="button" name="button" @click="administerFBDeny(userX)" v-if="userX.availableTest.feedbackTest">360 FeedBack</button>

            <button  class="bb" type="button" name="button" @click="administerBelbin(userX)" v-if="!userX.availableTest.belbinTest">Belbin Test</button>
            <button  class="cc" type="button" name="button" @click="administerBelbinDeny(userX)" v-if="userX.availableTest.belbinTest">Belbin Test</button>

            <button  class="bb" type="button" name="button" @click="administerFeedBackReportthis(userX)" v-if="!userX.availableTest.feedBackReport">FeedBack 360 Izvestaj</button>
            <button  class="cc" type="button" name="button" @click="administerFeedBackReportDenythis(userX)" v-if="userX.availableTest.feedBackReport">FeedBack 360 Izvestaj</button>

            <button  class="bb" type="button" name="button" @click="administerKpiPregledThis(userX)" v-if="!userX.availableTest.kpiPregled">Pregled Konacne Procene</button>
            <button  class="cc" type="button" name="button" @click="administerKpiPregledDenyThis(userX)" v-if="userX.availableTest.kpiPregled">Pregled Konacne Procene</button>

            <button  class="bb" type="button" name="button" @click="administerProcenaPregledThis(userX)" v-if="!userX.availableTest.procenaPregled">Pregled Procena Kandidata</button>
            <button  class="cc" type="button" name="button" @click="administerProcenaPregledDenyThis(userX)" v-if="userX.availableTest.procenaPregled">Pregled Procena Kandidata</button>
          </div>
        </div>

      </Tab>
    </TabNav>

  </div>


</template>

<script>
// @ is an alias to /src
// eslint-disable-next-line no-unused-vars
import {mapActions,mapGetters} from 'vuex';
// eslint-disable-next-line no-unused-vars
import TabNav from '@/components/TabNav.vue'
import Tab from '@/components/Tab.vue'

export default {
    computed: mapGetters(['companys',"users","user",'usersSameCompany']),
    components:{
      TabNav,
      Tab,
    },
    data(){
      return{
        selected: '',
        companyList:[],
        //each element is a list of users (different lists are for different companies)
        listOfListsOfUsers:[],
        validation:"",
        currentCompanyName:"",
        iMemory:0,
        loading:true,
      }
    },
    methods: {
      ...mapActions(['setLicniKpi','getCollegues','administerProcenaEdukacije','administerEducationEval','administerProcenaPregled','administerProcenaPregledDeny','administerFeedBackReport','administerFeedBackReportDeny','addPerformanceEval','addPerformanceEvalLicni','submitServiceAplication','getCompanies','getAllUsers','getProfile','administerVQTest','administerTLTest','administerStavoviTest','administerFBTest','administerBelbinTest','administerVQTestDeny','administerTLTestDeny','administerStavoviTestDeny','administerFBTestDeny','administerBelbinTestDeny','administerKpiPregled','administerKpiPregledDeny']),
      async setSelected(tab){
        this.loading=true;
        this.selected=tab;
        for (var i = 0; i < this.companyList.length; i++) {
          if(this.companyList[i]==this.selected){
            this.currentCompanyName=this.companyList[i];
            this.iMemory=i;
          }
        }

        await this.getCollegues(this.currentCompanyName);
        this.listOfListsOfUsers[this.iMemory]=[];

        for ( i = 0; i < this.usersSameCompany.length; i++) {
            this.listOfListsOfUsers[this.iMemory].push(this.usersSameCompany[i]);
        }
        this.loading=false;
      },
      async administerProcenaPregledThis(userAdminister){
        await this.administerProcenaPregled(userAdminister._id);
        this.validation=`Omogućen Pregled Procena za ${userAdminister.name}`;
        this.refreshListOfUsers();
      },
      async administerProcenaPregledDenyThis(userAdminister){
        await this.administerProcenaPregledDeny(userAdminister._id);
        this.validation=`Uklonjen Pregled Procena za  ${userAdminister.name}`;
        this.refreshListOfUsers();
      },
      async administerKpiPregledThis(userAdminister){
        await this.administerKpiPregled(userAdminister._id);
        this.validation=`Omogućen Pregled Kpi za ${userAdminister.name}`;
        this.refreshListOfUsers();
      },
      async administerKpiPregledDenyThis(userAdminister){
        await this.administerKpiPregledDeny(userAdminister._id);
        this.validation=`Uklonjen Pregled Kpi za ${userAdminister.name}`;
        this.refreshListOfUsers();
      },
      async administerFeedBackReportthis(userAdminister){
        await this.administerFeedBackReport(userAdminister._id);
        this.validation=`Omogućen FeedBack 360 Izvestaj za ${userAdminister.name}`;
        this.refreshListOfUsers();
      },
      async administerFeedBackReportDenythis(userAdminister){
        await this.administerFeedBackReportDeny(userAdminister._id);
        this.validation=`Uklonjen FeedBack 360 Izvestaj za ${userAdminister.name}`;
        this.refreshListOfUsers();
      },
      async  administerVQ(userAdminister){

        await this.administerVQTest(userAdminister._id);
        this.validation=`Omogućen VQ test za ${userAdminister.name}`;
        this.refreshListOfUsers();
      },
       async administerVQDeny(userAdminister){
      await  this.administerVQTestDeny(userAdminister._id);
        this.validation=`Uklonjen VQ test za ${userAdminister.name}`;
        this.refreshListOfUsers();
      },
       async  administerTL(userAdminister){
      await   this.administerTLTest(userAdminister._id);
        this.validation=`Omogućen NEO PI-R test za ${userAdminister.name}`;
        this.refreshListOfUsers();
      },
       async administerTLDeny(userAdminister){
        await this.administerTLTestDeny(userAdminister._id);
        this.validation=`Uklonjen NEO PI-R test za ${userAdminister.name}`;
        this.refreshListOfUsers();
      },
       async administerStavovi(userAdminister){
        this.validation=`Omogućen upitnik za istraživanje stavova zaposlenih za ${userAdminister.name}`;
        await this.administerStavoviTest(userAdminister._id);
        this.refreshListOfUsers();
      },
       async administerStavoviDeny(userAdminister){
        this.validation=`Uklonjen upitnik za istraživanje stavova zaposlenih za ${userAdminister.name}`;
        await  this.administerStavoviTestDeny(userAdminister._id);
        this.refreshListOfUsers();
      },
       async administerFB(userAdminister){
        await this.administerFBTest(userAdminister._id);
        this.validation=`Omogućen 360 FeedBack Upitnik za ${userAdminister.name}`;
        this.refreshListOfUsers();
      },
       async administerFBDeny(userAdminister){
        await this.administerFBTestDeny(userAdminister._id);
        this.validation=`Uklonjen 360 FeedBack Upitnik za ${userAdminister.name}`;
        this.refreshListOfUsers();
      },
       async administerBelbin(userAdminister){
        await this.administerBelbinTest(userAdminister._id);
        this.validation=`Omogućen belbin Upitnik za ${userAdminister.name}`;
        this.refreshListOfUsers();
      },
       async administerBelbinDeny(userAdminister){
        await this.administerBelbinTestDeny(userAdminister._id);
        this.validation=`Uklonjen belbin Upitnik za ${userAdminister.name}`;
        this.refreshListOfUsers();
      },
      // eslint-disable-next-line no-unused-vars

      async administerProcenaEdukacijeHere(){
        let data ={
          companyName:this.selected
        }
        console.log("pre admin");
        await this.administerEducationEval(data);
        console.log("posle admin");
        let tempComp;
        for (var i = 0; i < this.companys.length; i++) {
          if(this.companys[i].name == this.selected){
            tempComp=this.companys[i];
          }
        }
        await this.getCollegues(tempComp.name);

        for ( i = 0; i < this.usersSameCompany.length; i++) {
          await this.administerProcenaEdukacije(this.usersSameCompany[i]._id);
        }

        this.validation=`DODATA NOVA PROCENA EDUKACIJE`;
        this.refreshListOfUsers();
      },

      async administerPerformanceEval(usersList){

        if(confirm("Da li ste sigurni?")){

        for (var i = 0; i < usersList.length; i++) {
          let kpiData={
            userId:usersList[i]._id,
          }
          await this.addPerformanceEval(kpiData);
        }

        this.validation=`Dodat novi Performance Evaluation za selektovanu kompaniju. Treba uneti nove Licne KPI za svakog zaposlenog`;
        }
      },


      async administerPerformanceEvalLicni(usersList){

        if(confirm("Da li ste sigurni?")){

        for (var i = 0; i < usersList.length; i++) {

          let licniKpi=[];
          for (var  j = 0; j < usersList[i].performanceEvaluation[usersList[i].performanceEvaluation.length-1].licniKpi.length; j++) {
            let licni={
              name:usersList[i].performanceEvaluation[usersList[i].performanceEvaluation.length-1].licniKpi[j].name,
              ponder:usersList[i].performanceEvaluation[usersList[i].performanceEvaluation.length-1].licniKpi[j].ponder,
              comment:usersList[i].performanceEvaluation[usersList[i].performanceEvaluation.length-1].licniKpi[j].comment,
            }
            licniKpi.push(licni);
          }
          let kpiData={
            userId:usersList[i]._id,
            licniKpi,
          }
          console.log("LICNI: "+usersList[i].name);
          console.log(kpiData.licniKpi);
          await this.addPerformanceEvalLicni(kpiData);
        }

        this.validation=`Dodat novi Performance Evaluation za selektovanu kompaniju. Treba uneti nove Licne KPI za svakog zaposlenog`;
        }
      },

      async refreshListOfUsers(){
        await this.getCollegues(this.selected);
        this.listOfListsOfUsers=[];
        this.companyList=[];
        this.companys.forEach(company => {
          let list = [];
          if(company.name!="admin"){
          this.listOfListsOfUsers.push(list);
          this.companyList.push(company.name);
          }
        });

        for (var i = 0; i < this.usersSameCompany.length; i++) {
            this.listOfListsOfUsers[this.iMemory].push(this.usersSameCompany[i]);
        }

        // for (let i = 0; i < this.listOfListsOfUsers.length; i++) {
        //   let companyName = this.companyList[i];
        //   if(companyName!="admin"){
        //   for(let j = 0; j< this.users.length; j++){
        //     if(this.users[j].type === companyName){
        //       this.listOfListsOfUsers[i].push(this.users[j]);
        //     }
        //   }
        // }
        // }

      },


    },
    async created() {
      await this.getProfile();
      await  this.getCompanies();

      this.companys.forEach(company => {
        let list = [];

        if(company.name!="admin"){
        this.listOfListsOfUsers.push(list);
        this.companyList.push(company.name);
        }
      });

    },
}
</script>

<style lang="css" scoped>
.container {
  display: flex;
  margin-top: 5px;
}
.bb{
  background-color: #ffaaaa;
}
.cc{
  background-color: #aaffaa;
}
.dd{
  background-color: #aaaaff;
}
.userName{
  width:200px;
  border-color: #000410;
  border: 1px;
  text-decoration: none;
  border-style: solid;

}
.validation{
  margin-top: 5px;
  background-color: #00aaaa;
}

.loader {
  border: 16px solid #f3f3f3; /* Light grey */
  border-top: 16px solid #3498db; /* Blue */
  border-radius: 50%;
  width: 120px;
  height: 120px;
  animation: spin 2s linear infinite;

  margin:auto;
  margin-top: 100px;

}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

</style>
